.scrollup{
    position: fixed;
    right: 2.5rem;
    bottom: 20%;
    background-color: black;
    opacity: 0.8;
    padding: 0.3rem 0.5rem;
    border-radius: 0.4rem;
    z-index: var(--z-tooltip);
    transition: .4s;
}


.scrollup__icon{
    font-size: 1.5rem;
    color: var(--container-color);
}

.scrollup:hover{
    background-color: gray;
}

/* Show Scroll */

.show-scroll {
    bottom: 3rem;
}

li{
    list-style: none;
}

@media screen and (max-width: 992px){
  .scrollup{
    right: 1.5rem;
    padding: 0.25rem 0.4rem;
  }

  .scrollup__icon{
    font-size: 1.25rem;
  } 
}

@media screen and (max-width) {
    .show-scroll{
        bottom: 4rem;
    }
}

@media screen and(max-width: 350px) {
    .scrollup{
        right: 1rem;
    }
    
}